body {
    font-family: Arial, sans-serif;
    background-color: #fff;
    margin: 0;
    padding: 0;
}

.main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.skeleton {
    background-color: #f0f0f0;
    border-radius: 4px;
    margin-bottom: 10px;
}

.skeleton-header {
    width: 100%;
    height: 60px;
    background-color: #fff;
}

.skeleton-title {
    width: 60%;
    height: 40px;
    margin: 20px 0;
}

.skeleton-subtitle {
    width: 40%;
    height: 20px;
    margin-bottom: 20px;
}

.skeleton-button {
    width: 60%;
    height: 50px;
    margin: 10px 0;
}

.skeleton-text {
    width: 80%;
    height: 20px;
    margin: 10px 0;
}
